
.setup_top{width:15rem;height:1.76rem;background-color:#fff;position:relative;text-align: center
}
.setup_top div{position:absolute;width:1.2rem;height:1.2rem;background:url(../../static/img/pre.png)no-repeat;background-size:100% 100%;left:0.68rem;top:0.2rem;vertical-align:middle;
}
.setup_top span{line-height:1.76rem;font-size:.68rem;display:block;vertical-align:middle;
}
.setup_content>li{width: 100%;line-height:1.64rem;border-top:1px solid #dadada;text-indent:0.68rem;font-size:0.6rem;
}
.setup_content>li:first-of-type{line-height:3.6rem;display: inline-block;vertical-align: middle;
}
.setup_content>li:first-of-type img{width:2.8rem;height:2.8rem;display:inline-block;vertical-align:middle;margin-left:236px;
}
.setup_next{width:.32rem;height:.48rem;float:right;margin-right:.68rem;background:url(../../static/img/next.png)no-repeat;background-size:100% 100%;margin-top:.56rem;
}
.setup_out{background: #65C4AA;border-radius:.2rem;width:13.28rem;line-height:2rem;margin:.64rem auto 0;color:#fff;text-align: center;font-size:.68rem;
}



